.volume-container {
  position: relative; }
  .volume-container .slider {
    position: absolute;
    top: 15%;
    left: 10%;
    width: 80%;
    -webkit-appearance: none;
    height: 5px;
    outline: none; }
    .volume-container .slider::-webkit-slider-thumb {
