.switch-container {
  position: relative; }
  .switch-container .inner {
    position: absolute;
    top: 30%;
    left: 35%; }
    .switch-container .inner .switch {
      position: relative;
      display: inline-block;
      width: 77.7%;
      height: 25px; }
      .switch-container .inner .switch input {
        display: none; }
        .switch-container .inner .switch input:checked + .slider {
          background-color: #ec7063; }
        .switch-container .inner .switch input:checked + .slider:before {
          -webkit-transform: translateX(20px);
          -ms-transform: translateX(20px);
          transform: translateX(20px); }
      .switch-container .inner .switch .slider {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #d4d3d3;
        -webkit-transition: 0.4s;
        -o-transition: 0.4s;
        transition: 0.4s; }
        .switch-container .inner .switch .slider:before {
          position: absolute;
          content: '';
          height: 17px;
          width: 20px;
          left: 5px;
          bottom: 4px;
          background-color: white;
          -webkit-transition: 0.4s;
          -o-transition: 0.4s;
          transition: 0.4s; }



